import React from "react";
import styled from "styled-components";
import NewsBox from "../common/utility/NewsBoxjs";
import news1 from "../../image/news/news1.jpg";
import news2 from "../../image/news/news2.jpeg";
import news3 from "../../image/news/news3.jpg";
import news4 from "../../image/news/news4.jpg";
import news5 from "../../image/news/news5.jpg";
import news6 from "../../image/news/news6.jpg";


const NewsSectionTitle = styled.div`
    margin: 0px 20px 0px 20px;
    padding-bottom: 4px;
    border-bottom: 2px solid #4c5870;
    font-size: 48px;
    color: #4c5870;
`

const Row = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
    @media(max-width: 1440px) {
        flex-direction: column;
    }
`


const NewsSection = () => {
    return (
        <div>
            <NewsSectionTitle>NEWS</NewsSectionTitle>
            <Row>
                <NewsBox 
                title={" /  PUMA X MINECRAFT  2  8 "} 
                newsImg={news1} 
                tags={["CITY RIDER", "MINCRAFT", "PUMA"]}
                author={"Hans Tsai"}
                date={"Jan 07, 2022"}
                views={797}
                />
                <NewsBox 
                title={" / Nike PG 6  Paul George "} 
                newsImg={news2}
                tags={["BASKETBALL","NIKE","PAUL GEOOGE"]}
                author={"Mark Chou"}
                date={"Feb 02, 2022"}
                views={399}
                />
            </Row>
            <Row>
                <NewsBox 
                title={" /  DC  X PUMA Suede "} 
                newsImg={news3}
                tags={["DC","JUSTICE LEAGUE","PUMA"]}
                author={"Dr.J"}
                date={"DEC 27, 2021"}
                views={1023}
                />
                <NewsBox 
                title={" / 361 AG2 "} 
                newsImg={news4}
                tags={["361","AARON GORDON","AG2"]}
                author={"The Goat"}
                date={"Nov 07, 2021"}
                views={16788}
                />
            </Row>
            <Row>
                <NewsBox 
                title={" / UA Storm  HOVR Infinite 3HOVR Sonic 4 "} 
                newsImg={news5}
                tags={["HOVR INFINITE 3 STORM","HOVR SONIC 4 STORM","UNDER ARMOUR"]}
                author={"Steph"}
                date={"Nov 25, 2021"}
                views={677}
                />
                <NewsBox 
                title={" / Sacai X Nike  "} 
                newsImg={news6}
                tags={["CORTEZ","NIKE","NIKE X SACAI"]}
                author={"Sheep"}
                date={"Nov 01, 2021"}
                views={12501}
                />
            </Row>
        </div>
    );
};

export default NewsSection;